*, *::before, *::after {
     box-sizing: border-box;
 }

body {
    position: relative;
    min-height: 100%;
    margin: 0;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 1.0625rem;
    font-weight: 400;
    line-height: 1.4;
    color: #000;
    text-align: left;
    background-color: #fff;
}

header {
     display: block;
}

img {
     vertical-align: middle;
     border-style: none;
}

.container {
     width:100%;
     padding-right: 300px;
     padding-left: 500px;
     margin-right: auto;
     margin-left: auto;
}

.navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 0
}

.navbar-brand {
     display: inline-block;
     margin-right: 10px;
     font-size: 1.32812rem;
     line-height: inherit;
     white-space: nowrap;
}

.navbar-nav {
     box-sizing: border-box;
     padding: 10px;
     display: flex;
     padding-left: 0;
     vertical-align: middle;
     margin-bottom: 0;
     list-style: none;
     align-items: center;
}
.navbar-collapse {
     flex-basis: 100%;
     flex-grow: 1;
     align-items: center;
}



.navbar > .container {
     display:flex;
     flex-wrap: wrap;
     align-items: center;
     justify-content: space-between;
}

#main-nav .navbar {
     background-color: #000;
}

#main-nav #navbar-main {
     z-index: 2;
}

#main-nav .navbar-nav .nav-link {
     color: #30C2F8;
     text-decoration: none;
     font-size: 0.9rem;
     font-weight: 500;
     white-space: nowrap;
     align-items:center;
     padding-right: 20px;
}

@media (min-width: 992px) {
     .navbar-brand {
          
     }
}


@media (min-width: 992px) {
     .navbar-expand-lg .navbar-collapse {
          display: flex !important;
          flex-basis: auto;
     }
}

@media (min-width: 992px) {
     .justify-content-lg-end {
          justify-content: flex-end !important;
     }
}

@media (min-width: 576px) {
     .container {
          max-width: 540px;
     }
}

@media (min-width: 768px) {
     .container {
          max-width: 720px;
     }
}

@media (min-width: 992px) {
     .container {
          max-width: 960px;
     }
}

@media (min-width: 1200px) {
     .container {
          max-width: 1140px;
     }
}

@media (min-width: 992px) {
     .container {
          flex-wrap: nowrap;
     }
}
